@import '../../../styles/';

.nav {
  @include transition(.2s);
  position: absolute;
  right: 20px;
  top: 15px;
  font-size: 25px;
  color: $color-gold;
  cursor: pointer;
}

.nav-burger {
  z-index: 300;
  position: absolute;
  right: 0;
}

.nav-links {
  position: fixed;
  background: $color-black-nav;
  height: 100vh;
  width: 100%;
  flex-direction: column;
  clip-path: circle(50px at 90% -20%);
  -webkit-clip-path: circle(50px at 90% -10%);
  @include transition(all 1s ease-out);
  pointer-events: none;
  display: flex;
  list-style: none; 
  padding: 0 0.7vw;
  align-items: center;
  text-transform: uppercase;
  left: 0;
  top: 0;
  z-index: 200;
}

.nav-links.open {
  clip-path: circle(1000px at 90% -10%);
  -webkit-clip-path: circle(1000px at 90% -10%);
  pointer-events: all;
}
.nav-links li {
  opacity: 0;
}
.nav-links li:nth-child(1) {
  @include transition(all 0.5s ease 0.2s);
}
.nav-links li:nth-child(2) {
  @include transition(all 0.5s ease 0.4s);
}
.nav-links li:nth-child(3) {
  @include transition(all 0.5s ease 0.6s);
}
.nav-links li:nth-child(4) {
  @include transition(all 0.5s ease 0.7s);
}

li.fade {
  opacity: 1;
  margin-top: 120px;
}

.nav-links li a::before {
  content: "";
  display: block;
  height: 3px;
  width: 0%;
  background-color: $color-red;
  position: absolute;
  @include transition(all ease-in-out 0.25s);
  margin: 0 0 0 10%;
}

.nav-links li a {
  text-decoration: none;
  margin: 0 0.7vw;
}

.nav-links li a:hover {
  color: $color-red;
}
